  .panic{
    display: inline-block;
    text-decoration: none;
    background: red;
    color: #FFF;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    text-align: center;
    overflow: hidden;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.29);
    border-bottom: solid 2px grey;
    transition: .2s;
}

.panic:active{
    -webkit-transform: translateY(1px);
    transform: translateY(1px);
    box-shadow: inset 0px 3px 5px rgba(0, 0, 0, 0.29);
    border-bottom: none;
}

.panic input{
  display:none
}

.unpanic{
  transform: translateY(1px);
  -ms-transform: translateY(1px);
  -webkit-transform: translateY(1px);
  display: inline-block;
  text-decoration: none;
  background: rgb(255, 162, 162);
  color: #FFF;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  overflow: hidden;
  box-shadow: inset 0px 3px 5px rgba(0, 0, 0, 0.29);
  transition: .2s;
}

.unpanic:active{
  -webkit-transform: translateY(-1px);
  transform: translateY(1px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}

.unpanic input{
display:none
}

